.openContainer {
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, .6);
    z-index: 99;
    display: grid;
    place-items: center;
    animation: modal-bg-show .3s;
}

.closeContainer {
    display: none;
}

.modal {
    position: relative;
    width: 75dvw;
    max-width: 1200px;
    height: 75dvh;
    animation: modal-show .3s;
    border-radius: 20px;
    overflow: hidden;
}

.gradientTop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 40%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(9, 9, 9, 0.6) 65%, rgba(0, 0, 0, 1) 100%);
    color: white;
}

.gradientBot {
    position: absolute;
    top: 40%;
    left: 0;
    width: 100%;
    height: 60%;
    background: linear-gradient(0deg, rgba(0, 0, 0, 1) 0%, rgba(9, 9, 9, 0.6) 80%, rgba(0, 0, 0, 0) 100%);
    color: white;
}

.gradientTop h1 {
    position: relative;
    float: left;
    margin: 50px 0 0 50px
}

.gradientTop .rating {
    margin-left: 15px;
}

.gradientTop button {
    position: relative;
    float: right;
    outline: none;
    cursor: pointer;
    border: 0;
    margin: 15px 15px 0 0;
    font-weight: 700;
    background: rgba(0, 0, 0, 0);
    font-size: 2.5em;
    color: white;
}

.gradientBot img {
    position: relative;
    margin-left: 50px;
    float: left;
}

.gradientBot h3 {
    float: left;
    width: 800px;
    margin-right: auto;
    margin-left: 30px;
}

.gradientBot p {
    float: left;
    width: 800px;
    margin-left: 30px;
}

.gradientBot .link {
    float: left;
    margin-left: 30px;
}

@keyframes modal-bg-show {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes modal-show {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}